/* –û—Å–Ω–æ–≤–Ω—ã–µ —Å—Ç–∏–ª–∏ */
.ipoteka-container {
    background-color: rgba(37, 37, 37, 0.25);
    padding: 40px 20px;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ipoteka-card {
    background-color: #333333;
    border-radius: 15px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    padding: 40px;
    max-width: 900px;
    width: 100%;
}

.ipoteka-title {
    color: #fff;
    text-align: center;
    margin-bottom: 30px;
    font-size: 28px;
    font-weight: 700;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –ø–æ–ª–µ–π –≤–≤–æ–¥–∞ */
.ipoteka-inputs {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    margin-bottom: 30px;
}

.input-group {
    margin-bottom: 20px;
}

.input-group label {
    display: block;
    margin-bottom: 10px;
    color: #fff;
    font-weight: 600;
    font-size: 16px;
}

.input-value {
    font-size: 22px;
    font-weight: 700;
    color: #b8a668;
    margin: 10px 0;
}

.input-description {
    font-size: 14px;
    color: #7f8c8d;
    margin-top: 5px;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –ø–æ–ª–∑—É–Ω–∫–æ–≤ */
.ipoteka-slider {
    width: 100%;
    height: 8px;
    -webkit-appearance: none;
    background: #ecf0f1;
    border-radius: 5px;
    outline: none;
    margin: 15px 0;
}

.ipoteka-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 22px;
    height: 22px;
    background: #3498db;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.2s;
}

.ipoteka-slider::-webkit-slider-thumb:hover {
    background: #2980b9;
    transform: scale(1.1);
}

/* –°—Ç–∏–ª–∏ –¥–ª—è —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–≤ */
.ipoteka-results {
    background-color: #242b33;
    padding: 25px;
    border-radius: 10px;
    margin-bottom: 30px;
}

.result-item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid #e0e0e0;
}

.result-item:last-child {
    border-bottom: none;
    margin-bottom: 0;
    padding-bottom: 0;
}

.result-label {
    color: #D4C17F;
    font-weight: 600;
}

.result-value {
    color: #27ae60;
    font-weight: 700;
    font-size: 18px;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è —Ñ–æ—Ä–º—ã –∑–∞—è–≤–∫–∏ */
.ipoteka-form {
    margin-top: 30px;
    padding-top: 30px;
    border-top: 1px solid #e0e0e0;
}

.ipoteka-form h2 {
    color: #fff;
    margin-bottom: 20px;
    font-size: 22px;
    text-align: center;
}

.form-group {
    margin-bottom: 20px;
}

.form-group input {
    width: 100%;
    padding: 12px 15px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 16px;
    transition: border 0.3s;
}

.form-group input:focus {
    outline: none;
    border-color: #3498db;
}

.submit-btn {
    width: 100%;
    padding: 15px;
    background-color: #4CAF50; /* –Ø—Ä–∫–∏–π –∑–µ–ª–µ–Ω—ã–π */
    color: white !important; /* –í–∞–∂–Ω–æ: –±–µ–ª—ã–π —Ç–µ–∫—Å—Ç */
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s;
    box-shadow: 0 2px 5px rgba(0,0,0,0.2);
    opacity: 1 !important; /* –£–±–µ–¥–∏–º—Å—è, —á—Ç–æ –Ω–µ –ø—Ä–æ–∑—Ä–∞—á–Ω–∞—è */
    display: block; /* –ù–∞ —Å–ª—É—á–∞–π, –µ—Å–ª–∏ inline-—ç–ª–µ–º–µ–Ω—Ç */
    margin: 15px 0;
}

.submit-btn:hover {
    background-color: #45a049; /* –¢–µ–º–Ω–µ–µ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0,0,0,0.2);
}

.submit-btn:active {
    transform: translateY(0);
    background-color: #3e8e41;
}

/* –ï—Å–ª–∏ –ø—Ä–æ–±–ª–µ–º–∞ –≤ –Ω–∞—Å–ª–µ–¥–æ–≤–∞–Ω–∏–∏ —Å—Ç–∏–ª–µ–π, –¥–æ–±–∞–≤–∏–º "–∞—Ç–æ–º–∞—Ä–Ω—É—é" –∑–∞—â–∏—Ç—É */
button.submit-btn {
    background-color: #4CAF50 !important;
    color: white !important;
    opacity: 1 !important;
}

.disclaimer {
    font-size: 12px;
    color: #95a5a6;
    margin-top: 20px;
    text-align: center;
    line-height: 1.5;
}

/* –ê–¥–∞–ø—Ç–∏–≤–Ω–æ—Å—Ç—å */
@media (max-width: 768px) {
    .ipoteka-inputs {
        grid-template-columns: 1fr;
    }
    
    .ipoteka-card {
        padding: 20px;
    }
}